import React, { useState, useEffect, useCallback, useRef } from 'react'
import { useNavigate } from 'react-router-dom'
import { ArrowRight } from 'lucide-react'
import ReactCanvasConfetti from 'react-canvas-confetti'
import './welcome.css'
import Telegram from './telegram.svg'

const canvasStyles = {
  position: 'fixed',
  pointerEvents: 'none',
  width: '100%',
  height: '100%',
  top: 0,
  left: 0
}

export default function OnboardingFlow() {
  const [currentStep, setCurrentStep] = useState(0)
  const [isConfettiActive, setIsConfettiActive] = useState(false)
  const refAnimationInstance = useRef(null)
  const contentRef = useRef(null)
  const navigate = useNavigate()

  useEffect(() => {
    // Set initial height
    const setAppHeight = () => {
      const vh = window.innerHeight * 0.01
      document.documentElement.style.setProperty('--vh', `${vh}px`)
    }

    setAppHeight()
    window.addEventListener('resize', setAppHeight)
    return () => window.removeEventListener('resize', setAppHeight)
  }, [])

  const handleMain = useCallback(() => {
    navigate('/ProfilePage')
  }, [navigate])

  const getInstance = useCallback((instance) => {
    refAnimationInstance.current = instance
  }, [])

  const makeShot = useCallback((particleRatio, opts) => {
    refAnimationInstance.current &&
      refAnimationInstance.current({
        ...opts,
        origin: { y: 0.7 },
        particleCount: Math.floor(200 * particleRatio),
      })
  }, [])

  const fire = useCallback(() => {
    makeShot(0.25, { spread: 26, startVelocity: 55 })
    makeShot(0.2, { spread: 60 })
    makeShot(0.35, { spread: 100, decay: 0.91, scalar: 0.8 })
    makeShot(0.1, { spread: 120, startVelocity: 25, decay: 0.92, scalar: 1.2 })
    makeShot(0.1, { spread: 120, startVelocity: 45 })
  }, [makeShot])

  const steps = [
    {
      id: 'welcome',
      title: 'Welcome to the ',
      subtitle: 'VSC DATING',
      tag: ',    .',
      description: '  ,   ...',
      features: [
        {
          title: ' ',
          description: '    ,     , ?'
        },
        {
          title: ',  $?',
          description: '   , ,      '
        },
        {
          title: ' ',
          description: '  . ,     '
        }
      ]
    },
    {
      id: 'roadmap',
      title: ' ',
      items: [
        { text: 'MiniApp Telegram ', completed: true },
        { text: '  ', completed: true },
        { text: '  ', completed: true },
        { text: '  ', completed: true },
        { text: '****', completed: false },
        { text: '****', completed: false },
        { text: '****', completed: false },
        { text: '****', completed: false }
      ]
    },
    {
      id: 'socials',
      title: '    ',
      description: '     ,     ,       .',
      socials: [
        { name: 'Telegram ', icon: Telegram, link: 'https://t.me/vsc_dating' },
        { name: 'Telegram ', icon: Telegram, link: 'https://t.me/vsc_dating_chat' },
        { name: 'TikTok', icon: '/tiktok.svg', link: 'https://www.tiktok.com/@vsc_dating' },
        { name: 'X', icon: '/x.svg', link: 'https://twitter.com/vsc_dating' }
      ]
    },
    {
      id: 'final',
      title: ' !',
      description: '   .      VSC DATING!'
    }
  ]

  const handleNext = useCallback(() => {
    if (currentStep < steps.length - 1) {
      setCurrentStep(prevStep => prevStep + 1)
      if (currentStep === steps.length - 2) {
        setIsConfettiActive(true)
      }
    }
  }, [currentStep, steps.length])

  useEffect(() => {
    if (isConfettiActive) {
      fire()
      const timer = setInterval(fire, 2000)
      return () => clearInterval(timer)
    }
  }, [isConfettiActive, fire])

  const renderStep = () => {
    const step = steps[currentStep]

    switch (step.id) {
      case 'welcome':
        return (
          <div className="space-y-6">
            <div>
              <h1 className="text-3xl font-bold mb-2">
                {step.title}
                <span className="block text-[#4C9EEB] text-4xl">{step.subtitle}</span>
              </h1>
              <p className="text-[#4C9EEB] font-semibold text-base">{step.tag}</p>
              <p className="text-gray-400 mt-3 text-sm">{step.description}</p>
            </div>
            <div className="space-y-6">
              {step.features.map((feature, index) => (
                <div key={index} className="space-y-1">
                  <h3 className="text-[#4C9EEB] font-semibold text-base">{feature.title}</h3>
                  <p className="text-gray-400 text-sm leading-relaxed">{feature.description}</p>
                </div>
              ))}
            </div>
          </div>
        )

      case 'roadmap':
        return (
          <div className="space-y-6">
            <h1 className="text-3xl font-bold">{step.title}</h1>
            <div className="space-y-4">
              {step.items.map((item, index) => (
                <div key={index} className="flex items-center gap-3">
                  <div className={`w-8 h-8 rounded-full flex items-center justify-center flex-shrink-0 ${
                    item.completed ? 'bg-[#4C9EEB]' : 'bg-gray-800'
                  }`}>
                    {item.completed && (
                      <svg className="w-4 h-4 text-white" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                      </svg>
                    )}
                  </div>
                  <span className={`text-sm ${item.completed ? 'text-white' : 'text-gray-400'}`}>
                    {item.text}
                  </span>
                </div>
              ))}
            </div>
          </div>
        )

      case 'socials':
        return (
          <div className="space-y-6">
            <h1 className="text-3xl font-bold">{step.title}</h1>
            <div className="grid grid-cols-2 gap-3">
              {step.socials.map((social, index) => (
                <a
                  key={index}
                  href={social.link}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="bg-[#1A2C3D] rounded-xl p-4 flex flex-col items-center gap-2 hover:bg-[#243447] transition-colors"
                >
                  <div className="w-12 h-12 bg-[#4C9EEB]/10 rounded-lg flex items-center justify-center">
                    <img src={social.icon} alt={social.name} className="w-6 h-6" />
                  </div>
                  <span className="text-xs text-gray-300 text-center">{social.name}</span>
                </a>
              ))}
            </div>
            <p className="text-gray-400 text-center text-xs">{step.description}</p>
          </div>
        )

      case 'final':
        return (
          <div className="space-y-6 text-center">
            <h1 className="text-3xl font-bold">{step.title}</h1>
            <p className="text-gray-400 text-base">{step.description}</p>
            <div className="animate-bounce">
              <span className="text-5xl"></span>
            </div>
          </div>
        )

      default:
        return null
    }
  }

  return (
    <div className="onboarding-container bg-gradient-to-b from-[#0A1829] to-black">
      {/* Stars background */}
      <div className="absolute inset-0 pointer-events-none">
        <div className="stars-small" />
        <div className="stars-medium" />
        <div className="stars-large" />
      </div>

      {/* Navigation pills */}
      <div className="sticky top-0 z-10 w-full bg-gradient-to-b from-[#0A1829] to-transparent pb-4">
        <div className="nav-pills-container">
          <div className="nav-pills">
            <button 
              onClick={() => setCurrentStep(0)}
              className={`nav-pill ${currentStep === 0 ? 'bg-[#4C9EEB]' : 'bg-gray-800/50 backdrop-blur-sm'}`}
            >
               ?
            </button>
            <button 
              onClick={() => setCurrentStep(1)}
              className={`nav-pill ${currentStep === 1 ? 'bg-[#4C9EEB]' : 'bg-gray-800/50 backdrop-blur-sm'}`}
            >
               ?
            </button>
            <button 
              onClick={() => setCurrentStep(2)}
              className={`nav-pill ${currentStep === 2 ? 'bg-[#4C9EEB]' : 'bg-gray-800/50 backdrop-blur-sm'}`}
            >
               
            </button>
          </div>
        </div>
      </div>

      {/* Content area */}
      <div className="content-container">
        <div className="content-wrapper">
          {renderStep()}
        </div>
      </div>

      {/* Footer with button */}
      <div className="sticky bottom-0 w-full px-4 py-4 bg-gradient-to-t from-black to-transparent">
        {currentStep < steps.length - 1 ? (
          <button onClick={handleNext} className="next-button">
            
            <ArrowRight className="w-5 h-5" />
          </button>
        ) : (
          <button onClick={handleMain} className="next-button bg-green-500 hover:bg-green-600">
             
          </button>
        )}
      </div>

      <ReactCanvasConfetti
        refConfetti={getInstance}
        style={canvasStyles}
      />
    </div>
  )
}

