import React, { useState, useEffect } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import { Pencil, Zap, Flame, Loader, ChevronRight, Heart, User, Sparkles } from 'lucide-react'
import { createClient } from '@supabase/supabase-js'
import { useNavigate } from 'react-router-dom'
import TutorialOverlay from './components/TutorialOverlay';
import './profile.css'

const supabase = createClient('https://kjzpfvjyalipijwoesoh.supabase.co', 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6ImtqenBmdmp5YWxpcGlqd29lc29oIiwicm9sZSI6ImFub24iLCJpYXQiOjE3Mjg4MzU3ODMsImV4cCI6MjA0NDQxMTc4M30.JaIDZ6LDvHFa4jptS1BfBTzjMOcAu7ig1PqTHWgqJEg')

const ExpandableAbout = ({ bio }) => {
  const [isExpanded, setIsExpanded] = useState(false)
  const truncatedBio = bio.length > 50 ? bio.slice(0, 50) + '...' : bio
  
  return (
    <div className="w-full bg-gray-900/50 backdrop-blur-sm rounded-xl overflow-hidden mb-12">
      <button 
        onClick={() => setIsExpanded(!isExpanded)}
        className="w-full p-4 text-left"
      >
        <div className="flex items-start gap-3">
          <span className="text-4xl text-blue-500 flex-shrink-0">"</span>
          <span className="text-gray-400 text-base leading-relaxed">
            {isExpanded ? bio : truncatedBio}
          </span>
          <ChevronRight 
            className={`w-5 h-5 text-gray-600 flex-shrink-0 transition-transform ${
              isExpanded ? 'rotate-90' : ''
            }`}
          />
        </div>
      </button>
    </div>
  )
}

export default function ProfilePage() {
  const [profile, setProfile] = useState(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState(null)
  const [showTutorial, setShowTutorial] = useState(false)
  const navigate = useNavigate()

  const handleMainPage = () => {
    navigate('/MainPage')
  }

  useEffect(() => {
    async function fetchProfile() {
      try {
        if (window.Telegram && window.Telegram.WebApp) {
          const tg = window.Telegram.WebApp
          
          const user = tg.initDataUnsafe.user
          if (user && user.id) {
            const { data, error } = await supabase
              .from('datingbase')
              .select('*')
              .eq('user_id', user.id)
              .single()

            if (error) throw error
            setProfile(data)
            if (data?.tutorial === 'yes') {
              setShowTutorial(true)
            }
          } else {
            throw new Error('No user data available in Telegram WebApp')
          }
        } else {
          throw new Error('Telegram WebApp not available')
        }
      } catch (error) {
        setError(error.message)
      } finally {
        setLoading(false)
      }
    }

    fetchProfile()
  }, [])

  const handleTutorialComplete = async () => {
    try {
      if (profile && profile.user_id) {
        const { error } = await supabase
          .from('datingbase')
          .update({ tutorial: 'no' })
          .eq('user_id', profile.user_id)

        if (error) throw error

        setShowTutorial(false)
        setProfile(prevProfile => ({ ...prevProfile, tutorial: 'no' }))
      }
    } catch (error) {
      console.error('Failed to update tutorial status:', error.message)
    }
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-gradient-to-b from-[#0A1829] to-black text-white flex items-center justify-center">
        <Loader className="w-8 h-8 animate-spin" />
      </div>
    )
  }

  if (error) {
    return (
      <div className="min-h-screen bg-gradient-to-b from-[#0A1829] to-black text-white flex items-center justify-center">
        <p className="text-red-500">Error: {error}</p>
      </div>
    )
  }

  if (!profile) {
    return (
      <div className="min-h-screen bg-gradient-to-b from-[#0A1829] to-black text-white flex items-center justify-center">
        <p>No profile data found</p>
      </div>
    )
  }

  const completionPercentage = 0 // This should be calculated based on profile completeness
  const completionArc = (completionPercentage / 100) * 301.6 // 301.6 is the circumference of the circle

  return (
    <div className="min-h-screen bg-gradient-to-b from-[#0A1829] to-black text-white relative">
      <AnimatePresence>
        {showTutorial && (
          <TutorialOverlay onComplete={handleTutorialComplete} />
        )}
      </AnimatePresence>

      <div className="pt-20 px-4 relative z-10">
        <div className="relative w-32 h-32 mx-auto mb-4">
          <div className="w-full h-full rounded-full overflow-hidden border-2 border-gray-800">
            <img
              src={profile.image_url}
              alt="Profile"
              className="w-full h-full object-cover"
            />
          </div>
          <div className="absolute inset-0 -rotate-90">
            <svg viewBox="0 0 100 100" className="w-full h-full">
              <circle
                cx="50"
                cy="50"
                r="48"
                fill="none"
                stroke="#FFA500"
                strokeWidth="4"
                strokeDasharray={`${completionArc} 301.6`}
                className="transform rotate-0"
              />
            </svg>
          </div>
          <div className="absolute bottom-0 right-0 bg-[#FFA500] rounded-lg px-2 py-1 text-sm">
            {completionPercentage}%
          </div>
        </div>

        <h1 className="text-3xl font-bold text-center mb-6">
          {profile.name}, {profile.age}
        </h1>

        <div className="flex justify-center gap-4 mb-8">
          <button className="flex items-center gap-2 px-6 py-3 bg-gray-800 rounded-full">
            <Pencil className="w-5 h-5" />
            
          </button>
          <button className="w-12 h-12 flex items-center justify-center bg-gray-800 rounded-full text-xl">
            
          </button>
        </div>

        <ExpandableAbout bio={profile.bio || "  "} />

        <div className="grid grid-cols-2 gap-4 mt-8">
          <button 
            onClick={handleMainPage}
            className="bg-purple-900/50 rounded-xl p-6 relative hover:bg-purple-900/70 transition-colors flex flex-col items-center text-center"
          >
            <div className="w-12 h-12 bg-purple-500 rounded-full flex items-center justify-center mb-4">
              <Flame className="w-6 h-6" />
            </div>
            <h3 className="text-xl font-semibold"></h3>
          </button>
          
          <button 
            onClick={() => navigate('/InteresPage')}
            className="bg-amber-900/50 rounded-xl p-6 hover:bg-amber-900/70 transition-colors flex flex-col items-center text-center w-full"
          >
            <div className="w-12 h-12 bg-amber-500 rounded-full flex items-center justify-center mb-4">
              <Sparkles className="w-6 h-6" />
            </div>
            <h3 className="text-xl font-semibold"></h3>
          </button>
        </div>
      </div>

      <div className="nav-container fixed bottom-0 left-0 right-0 bg-black/50 backdrop-blur-sm pb-6">
        <div className="nav-content flex justify-around items-center p-4">
          <button className="p-3 text-white/80 hover:text-white">
            <Zap className="w-6 h-6" />
          </button>
          
          <button className="p-3 text-white/80 hover
:text-white relative">
            <Heart onClick={() => navigate('/LikesPage')}  className="w-6 h-6" />
            <span className="absolute -top-1 -right-1 w-5 h-5 bg-red-500 rounded-full text-white text-xs flex items-center justify-center">
              8
            </span>
          </button>
          
          <button 
            onClick={() => navigate('/ProfilePage')} 
            className="p-3 text-white/80 hover:text-white"
          >
            <User className="w-6 h-6" />
          </button>
        </div>
      </div>
    </div>
  )
}

