  export default function CitySelect({ value, onChange }) {
    const [search, setSearch] = useState(value);
    const [isOpen, setIsOpen] = useState(false);
    const wrapperRef = useRef(null);
  
    const filteredCities = cities.filter(city =>
      city.toLowerCase().includes(search.toLowerCase())
    );
  
    const handleSelect = (city) => {
      setSearch(city);
      onChange(city);
      setIsOpen(false);
    };

    const handleInputChange = (e) => {
      const newValue = e.target.value;
      setSearch(newValue);
      onChange(newValue);
      setIsOpen(true);
    };

    useEffect(() => {
      function handleClickOutside(event) {
        if (wrapperRef.current && !wrapperRef.current.contains(event.target)) {
          setIsOpen(false);
        }
      }
      document.addEventListener("mousedown", handleClickOutside);
      return () => {
        document.removeEventListener("mousedown", handleClickOutside);
      };
    }, [wrapperRef]);
  
    return (
      <div className="relative" ref={wrapperRef}>
        <label className="block text-sm font-medium text-gray-300 mb-2">
          
        </label>
        <input
          type="text"
          value={search}
          onChange={handleInputChange}
          onFocus={() => setIsOpen(true)}
          className="w-full px-4 py-2 rounded-lg border text-white bg-gray-800 border-gray-700 focus:outline-none focus:border-blue-500"
          placeholder="   "
        />
        {isOpen && (
          <div className="absolute z-10 w-full mt-1 bg-gray-800 border border-gray-700 rounded-lg shadow-lg max-h-48 overflow-auto">
            {filteredCities.map((city, index) => (
              <div
                key={index}
                className="px-4 py-2 hover:bg-gray-700 cursor-pointer text-white"
                onClick={() => handleSelect(city)}
              >
                {city}
              </div>
            ))}
            {filteredCities.length === 0 && (
              <div className="px-4 py-2 text-gray-400">
                  
              </div>
            )}
          </div>
        )}
      </div>
    );
  }
  

