import React, { useState, useEffect } from 'react';
import Lottie from 'lottie-react'
import { useNavigate } from 'react-router-dom'
import { createClient } from '@supabase/supabase-js';
import wavingHandAnimation from './stickers/test.json';
import CitySelect from './CitySelect';

const supabase = createClient('https://kjzpfvjyalipijwoesoh.supabase.co', 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6ImtqenBmdmp5YWxpcGlqd29lc29oIiwicm9sZSI6ImFub24iLCJpYXQiOjE3Mjg4MzU3ODMsImV4cCI6MjA0NDQxMTc4M30.JaIDZ6LDvHFa4jptS1BfBTzjMOcAu7ig1PqTHWgqJEg');

export default function RegistrationForm() {
  const navigate = useNavigate()
  const [loading, setLoading] = useState(true);
  const [step, setStep] = useState(1);
  const [user, setUser] = useState(null);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [imageError, setImageError] = useState('');
  const [formData, setFormData] = useState({
    name: '',
    age: '',
    city: '',
    sex: '',
    bio: '',
    image: null
  });

  useEffect(() => {
    if (window.Telegram && window.Telegram.WebApp) {
      const tg = window.Telegram.WebApp;
      tg.ready();
      tg.expand();
      tg.enableClosingConfirmation();

      const user = tg.initDataUnsafe.user;
      setUser(user);
      checkExistingUser(user?.id);
    }
  }, []);

  const checkExistingUser = async (userId) => {
    if (!userId) {
      setLoading(false);
      return;
    }

    const { data, error } = await supabase
      .from('datingbase')
      .select('user_id')
      .eq('user_id', userId)
      .single();

    if (data) {
      navigate('/ProfilePage');
      console.log('User already exists, navigate to ProfilePage');
    } else {
      setLoading(false);
    }
  };

  const handleNext = () => {
    setStep(prev => prev + 1);
  };

  const handleBack = () => {
    setStep(prev => prev - 1);
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleImageChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      const fileType = file.type.split('/')[1].toLowerCase();
      if (['png', 'jpg', 'jpeg', 'gif'].includes(fileType)) {
        setFormData(prev => ({ ...prev, image: file }));
        setImageError('');
      } else {
        setFormData(prev => ({ ...prev, image: null }));
        setImageError('  (PNG, JPG,  GIF)');
      }
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!user || isSubmitting) return;

    setIsSubmitting(true);

    try {
      let image_url = '';
      if (formData.image) {
        const { data, error } = await supabase.storage
          .from('images')
          .upload(`${user.id}/${formData.image.name}`, formData.image);
        
        if (error) {
          console.error('Error uploading image:', error);
          return;
        }
        
        image_url = supabase.storage.from('images').getPublicUrl(`${user.id}/${formData.image.name}`).data.publicUrl;
      }

      const { data, error } = await supabase
        .from('datingbase')
        .insert({
          user_id: user.id,
          name: formData.name,
          age: formData.age,
          city: formData.city,
          sex: formData.sex,
          bio: formData.bio,
          interescity: formData.city,
          interesold: formData.age,
          interespol: "",
          tutorial: "yes",
          image_url
        });

      if (error) {
        console.error('Error inserting data:', error);
      } else {
        navigate('/ProfilePage');
        console.log('Registration successful, navigate to ProfilePage');
      }
    } catch (error) {
      console.error('Error during registration:', error);
    } finally {
      setIsSubmitting(false);
    }
  };

  if (loading) {
    return <div className="flex items-center justify-center h-screen text-white">...</div>;
  }

  if (!user) {
    return <div className="flex items-center justify-center h-screen text-white">  </div>;
  }




  return (
    <div className="min-h-screen bg-gradient-to-b from-[#0A1829] to-black text-white flex flex-col items-center justify-start pt-12 p-4">
      <div className="flex flex-col items-center mb-8">
        <Lottie
          loop
          animationData={wavingHandAnimation}
          play
          style={{ width: 150, height: 150 }}
        />
      </div>
      <div className="w-full max-w-md space-y-6">
        <form onSubmit={handleSubmit} className="space-y-4">
          {step === 1 && (
            <div className="space-y-4">
              <h2 className="text-lg text-center">  ?</h2>
              <input
                type="text"
                name="name"
                value={formData.name}
                onChange={handleInputChange}
                placeholder=" "
                className="w-full px-4 py-3 rounded-lg bg-gray-800/50 border border-gray-700 text-white placeholder-gray-400 focus:outline-none focus:border-gray-500"
                required
              />
              <button
                type="button"
                onClick={handleNext}
                disabled={!formData.name}
                className="w-full py-3 rounded-lg bg-[#874fff] text-white font-medium disabled:opacity-50 disabled:cursor-not-allowed"
              >
                
              </button>
            </div>
          )}

          {step === 2 && (
            <div className="space-y-4">
              <h2 className="text-lg text-center">  ?</h2>
              <input
                type="number"
                name="age"
                value={formData.age}
                onChange={handleInputChange}
                placeholder="  "
                min="16"
                className="w-full px-4 py-3 rounded-lg bg-gray-800/50 border border-gray-700 text-white placeholder-gray-400 focus:outline-none focus:border-gray-500"
                required
              />
              <div className="space-y-2">
                <button
                  type="button"
                  onClick={handleNext}
                  disabled={!formData.age}
                  className="w-full py-3 rounded-lg bg-[#874fff] text-white font-medium disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  
                </button>
                <button
                  type="button"
                  onClick={handleBack}
                  className="w-full py-3 rounded-lg bg-white/10 text-white font-medium"
                >
                  
                </button>
              </div>
            </div>
          )}

          {step === 3 && (
            <div className="space-y-4">
              <h2 className="text-lg text-center">  </h2>
              <CitySelect value={formData.city} onChange={(value) => handleInputChange({ target: { name: 'city', value } })} />
              <div className="space-y-2">
                <button
                  type="button"
                  onClick={handleNext}
                  disabled={!formData.city}
                  className="w-full py-3 rounded-lg bg-[#874fff] text-white font-medium disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  
                </button>
                <button
                  type="button"
                  onClick={handleBack}
                  className="w-full py-3 rounded-lg bg-white/10 text-white font-medium"
                >
                  
                </button>
              </div>
            </div>
          )}

          {step === 4 && (
            <div className="space-y-4">
              <h2 className="text-lg text-center"> </h2>
              <div className="grid grid-cols-2 gap-4">
                <button
                  type="button"
                  onClick={() => handleInputChange({ target: { name: 'sex', value: '' }})}
                  className={`py-3 px-4 rounded-lg font-medium ${
                    formData.sex === '' 
                      ? 'bg-[#874fff56] text-white' 
                      : 'bg-gray-800/50 text-gray-300'
                  }`}
                >
                   
                </button>
                <button
                  type="button"
                  onClick={() => handleInputChange({ target: { name: 'sex', value: '' }})}
                  className={`py-3 px-4 rounded-lg font-medium ${
                    formData.sex === '' 
                      ? 'bg-[#874fff56] text-white' 
                      : 'bg-gray-800/50 text-gray-300'
                  }`}
                >
                   
                </button>
              </div>
              <div className="space-y-2">
                <button
                  type="button"
                  onClick={handleNext}
                  disabled={!formData.sex}
                  className="w-full py-3 rounded-lg bg-[#874fff] text-white font-medium disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  
                </button>
                <button
                  type="button"
                  onClick={handleBack}
                  className="w-full py-3 rounded-lg bg-white/10 text-white font-medium"
                >
                  
                </button>
              </div>
            </div>
          )}

          {step === 5 && (
            <div className="space-y-4">
              <h2 className="text-lg text-center">  </h2>
              <textarea
                name="bio"
                value={formData.bio}
                onChange={handleInputChange}
                placeholder="  "
                rows="3"
                className="w-full px-4 py-3 rounded-lg bg-gray-800/50 border border-gray-700 text-white placeholder-gray-400 focus:outline-none focus:border-gray-500"
                required
              ></textarea>
              <div className="space-y-2">
                <button
                  type="button"
                  onClick={handleNext}
                  disabled={!formData.bio}
                  className="w-full py-3 rounded-lg bg-[#874fff] text-white font-medium disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  
                </button>
                <button
                  type="button"
                  onClick={handleBack}
                  className="w-full py-3 rounded-lg bg-white/10 text-white font-medium"
                >
                  
                </button>
              </div>
            </div>
          )}

          {step === 6 && (
            <div className="space-y-4">
              <h2 className="text-lg text-center">  </h2>
              <div className="border-2 border-dashed text-gray-400 border-gray-700 rounded-lg p-4 text-center">
                <input
                  type="file"
                  accept="image/png,image/jpeg,image/gif"
                  onChange={handleImageChange}
                  className="hidden"
                  id="file-upload"
                  required
                />
                <label
                  htmlFor="file-upload"
                  className="cursor-pointer text-blue-400 hover:text-blue-300 text-sm"
                >
                   
                </label>
                <p className="mt-1 text-xs text-gray-500">PNG, JPG, GIF  10MB</p>
                {formData.image && !imageError && (
                  <p className="mt-1 text-xs text-green-400">
                     : {formData.image.name}
                  </p>
                )}
                {imageError && (
                  <p className="mt-1 text-xs text-red-500">
                    {imageError}
                  </p>
                )}
              </div>
              <div className="space-y-2">
                <button
                  type="submit"
                  disabled={isSubmitting || !formData.image}
                  className="w-full py-3 rounded-lg bg-[#874fff] text-white font-medium disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  {isSubmitting ? '...' : ''}
                </button>
                <button
                  type="button"
                  onClick={handleBack}
                  className="w-full py-3 rounded-lg bg-white/10 text-white font-medium"
                >
                  
                </button>
              </div>
            </div>
          )}
        </form>
      </div>
    </div>
  );
}

