export default function Filters({ onClose }) {
  const [gender, setGender] = useState("all")
  const [ageRange, setAgeRange] = useState([18, 20])
  const [city, setCity] = useState("")
  const [isOpen, setIsOpen] = useState(false)
  const [isUpdating, setIsUpdating] = useState(false)
  const [updateStatus, setUpdateStatus] = useState(null)
  const navigate = useNavigate()

  const toggleDropdown = () => setIsOpen(!isOpen)

  const selectCity = (selectedCity) => {
    setCity(selectedCity)
    setIsOpen(false)
  }

  const handleDone = async () => {
    setIsUpdating(true)
    setUpdateStatus(null)

    // Get user_id from Telegram JS API
    let user_id
    if (window.Telegram && window.Telegram.WebApp) {
      user_id = window.Telegram.WebApp.initDataUnsafe.user.id
    } else {
      console.error('Telegram WebApp not available')
      setUpdateStatus('error')
      setIsUpdating(false)
      return
    }

    try {
      const { error } = await supabase
        .from('datingbase')
        .update({
          interespol: gender === 'girls' ? '' : gender === 'guys' ? '' : '',
          interesold: ageRange[1],
          interescity: city
        })
        .eq('user_id', user_id)

      if (error) throw error

      setUpdateStatus('success')
      setTimeout(() => {
        navigate('/ProfilePage')
      }, 1500)
    } catch (error) {
      console.error('Error updating preferences:', error)
      setUpdateStatus('error')
    } finally {
      setIsUpdating(false)
    }
  }

  return (
    <div className="fixed inset-0 bg-black text-white flex flex-col h-full">
      <div className="flex items-center justify-between p-4 border-b border-gray-800">
        <h1 className="text-xl font-semibold">,   </h1>
        <button onClick={() => navigate('/ProfilePage')} className="text-gray-400 hover:text-white">
          <X className="h-6 w-6" />
        </button>
      </div>

      <div className="flex-grow overflow-y-auto p-4 space-y-6">
        <div className="space-y-2">
          <h3 className="text-sm text-gray-400"> </h3>
          <div className="flex gap-2">
            {[
              { value: "girls", label: "" },
              { value: "guys", label: "" },
              { value: "all", label: "" }
            ].map((option) => (
              <button
                key={option.value}
                className={`flex-1 py-2 px-4 rounded-full border ${
                  gender === option.value
                    ? "bg-white text-black"
                    : "border-gray-600 text-white"
                }`}
                onClick={() => setGender(option.value)}
              >
                {option.label}
              </button>
            ))}
          </div>
        </div>

        <div className="space-y-4">
          <div className="space-y-2">
            <div className="flex justify-between">
              <h3 className="text-sm text-gray-400"></h3>
              <span className="text-sm text-white">
                {ageRange[1]}
              </span>
            </div>
            <input
              type="range"
              min="18"
              max="60"
              value={ageRange[1]}
              onChange={(e) => setAgeRange([ageRange[0], parseInt(e.target.value)])}
              className="w-full appearance-none h-1 rounded-full bg-gray-600 [&::-webkit-slider-thumb]:appearance-none [&::-webkit-slider-thumb]:w-5 [&::-webkit-slider-thumb]:h-5 [&::-webkit-slider-thumb]:rounded-full [&::-webkit-slider-thumb]:bg-white [&::-webkit-slider-thumb]:border-2 [&::-webkit-slider-thumb]:border-blue-500 [&::-moz-range-thumb]:w-5 [&::-moz-range-thumb]:h-5 [&::-moz-range-thumb]:rounded-full [&::-moz-range-thumb]:bg-white [&::-moz-range-thumb]:border-2 [&::-moz-range-thumb]:border-blue-500"
              style={{
                background: `linear-gradient(to right, #3b82f6 0%, #3b82f6 ${((ageRange[1] - 18) / 42) * 100}%, #4b5563 ${((ageRange[1] - 18) / 42) * 100}%, #4b5563 100%)`
              }}
            />
          </div>
        </div>

        <div className="space-y-2">
          <h3 className="text-sm text-gray-400"></h3>
          <div className="relative">
            <button
              onClick={toggleDropdown}
              className="w-full p-2 rounded-lg bg-gray-800 border border-gray-600 text-white focus:outline-none focus:ring-2 focus:ring-white flex justify-between items-center"
            >
              {city || " "}
              <ChevronDown className={`h-4 w-4 transition-transform ${isOpen ? 'rotate-180' : ''}`} />
            </button>
            {isOpen && (
              <div className="absolute z-10 w-full mt-1 bg-gray-800 border border-gray-600 rounded-lg shadow-lg max-h-60 overflow-y-auto">
                {cities.map((cityOption) => (
                  <button
                    key={cityOption}
                    className="w-full text-left px-4 py-2 hover:bg-gray-700 focus:outline-none focus:bg-gray-700"
                    onClick={() => selectCity(cityOption)}
                  >
                    {cityOption}
                  </button>
                ))}
              </div>
            )}
          </div>
        </div>
      </div>

      <div className="p-4 border-t border-gray-800">
        <button 
          onClick={handleDone}
          disabled={isUpdating}
          className={`w-full py-3 px-4 rounded-full text-lg font-semibold transition duration-200 ${
            isUpdating 
              ? 'bg-gray-500 text-gray-300 cursor-not-allowed' 
              : 'bg-white text-black hover:bg-gray-200'
          }`}
        >
          {isUpdating ? '...' : ''}
        </button>
        {updateStatus === 'success' && (
          <p className="mt-2 text-center text-green-500">  !</p>
        )}
        {updateStatus === 'error' && (
          <p className="mt-2 text-center text-red-500">   .   .</p>
        )}
      </div>
    </div>
  )
}


