import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Flame, Sparkles } from 'lucide-react'

const TooltipWithFinger = ({ text, position }) => (
  <div className={`absolute ${position} z-50 flex flex-col items-center`}>
    <div className="bg-[#1E1B4B]/80 text-white px-4 py-2 rounded-lg backdrop-blur-sm text-sm mb-4">
      {text}
    </div>
  </div>
);

const TutorialStep = ({ isVisible, children, onClick }) => {
  if (!isVisible) return null;
  
  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
      className="fixed inset-0 z-50 flex items-center justify-center"
      onClick={onClick}
    >
      <div className="absolute inset-0 bg-black/80 backdrop-blur-md" />
      {children}
    </motion.div>
  );
};

export const TutorialOverlay = ({ onComplete }) => {
  const [currentStep, setCurrentStep] = useState(1);
  const [isVisible, setIsVisible] = useState(true);

  useEffect(() => {
    if (currentStep === 1) {
      const timer = setTimeout(() => {
        setCurrentStep(2);
      }, 5000);
      return () => clearTimeout(timer);
    }
  }, [currentStep]);

  const handleComplete = () => {
    setIsVisible(false);
    if (onComplete) {
      onComplete();
    }
  };

  if (!isVisible) return null;

  return (
    <AnimatePresence>
      {currentStep === 1 && (
        <TutorialStep isVisible={true} onClick={() => setCurrentStep(2)}>
          <motion.div
            className="absolute left-4 right-4 top-1/2 -translate-y-1/2 pointer-events-none"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
          >
            <div className="grid grid-cols-2 gap-4">
              <div className="relative">
                <motion.div
                  className="top-32 bg-purple-900/50 rounded-xl p-6 relative flex flex-col items-center text-center"
                  initial={{ opacity: 0.3 }}
                  animate={{ opacity: 1 }}
                  whileTap={{ scale: 0.95 }}
                >
                  <div className="w-12 h-12 bg-purple-500 rounded-full flex items-center justify-center mb-4">
                    <Flame className="w-6 h-6" />
                  </div>
                  <h3 className="text-xl font-semibold text-white"></h3>
                </motion.div>
                <motion.span 
                  className="top-60 text-5xl absolute bottom-0 left-1/2 -translate-x-1/2 translate-y-full mt-4"
                  animate={{ y: [0, -10, 0] }}
                  transition={{ repeat: Infinity, duration: 1.5 }}
                >
                  
                </motion.span>
              </div>
            </div>
          </motion.div>
        </TutorialStep>
      )}

      {currentStep === 2 && (
        <TutorialStep isVisible={true} onClick={handleComplete}>
          <motion.div
            className="absolute left-4 right-4 top-1/2 -translate-y-1/2 pointer-events-none"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
          >
            <div className="grid grid-cols-2 gap-4">
              <div className="col-start-2 relative">
                <TooltipWithFinger
                  className="top-"
                  text="   .    ,      ."
                  position="w-80 h-80 top-11 left-7 -translate-x-1/2"
                />
                <motion.div
                  className="top-32 bg-amber-900/50 rounded-xl p-6 relative flex flex-col items-center text-center"
                  initial={{ opacity: 0.3 }}
                  animate={{ opacity: 1 }}
                  whileTap={{ scale: 0.95 }}
                >
                  <div className="w-12 h-12 bg-amber-500 rounded-full flex items-center justify-center mb-4">
                    <Sparkles className="w-6 h-6" />
                  </div>
                  <h3 className="text-xl font-semibold text-white"></h3>
                </motion.div>
                <motion.span 
                  className="top-60 text-5xl absolute bottom-0 left-14 -translate-x-5 translate-y-full mt-4"
                  animate={{ y: [0, -10, 0] }}
                  transition={{ repeat: Infinity, duration: 1.5 }}
                >
                  
                </motion.span>
              </div>
            </div>
          </motion.div>
        </TutorialStep>
      )}
    </AnimatePresence>
  );
};

export default TutorialOverlay;

