import React, { useState, useEffect, useRef } from 'react'
import { createClient } from '@supabase/supabase-js'
import { ArrowLeft, X, Heart, Search, MessageCircle, User, Zap } from 'lucide-react'
import { useNavigate } from 'react-router-dom'
import './likes-page.css'

const supabase = createClient('https://kjzpfvjyalipijwoesoh.supabase.co', 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6ImtqenBmdmp5YWxpcGlqd29lc29oIiwicm9sZSI6ImFub24iLCJpYXQiOjE3Mjg4MzU3ODMsImV4cCI6MjA0NDQxMTc4M30.JaIDZ6LDvHFa4jptS1BfBTzjMOcAu7ig1PqTHWgqJEg')

const PARTICLE_COUNT = 5000;
const PARTICLE_PROP_COUNT = 5; 
const ANIMATION_DURATION = 500;

export default function LikesPage({ onBack }) {
  const [likes, setLikes] = useState([])
  const [selectedUser, setSelectedUser] = useState(null)
  const [isLoading, setIsLoading] = useState(true)
  const canvasRef = useRef(null)
  const animationRef = useRef(null)
  const navigate = useNavigate()

  useEffect(() => {
    if (window.Telegram && window.Telegram.WebApp) {
      const tg = window.Telegram.WebApp;
      tg.disableVerticalSwipes();
    }
    fetchLikes()
  }, [])

  async function fetchLikes() {
    const currentUserId = window.Telegram?.WebApp?.initDataUnsafe?.user?.id
    if (!currentUserId) {
      console.error('User ID not available')
      return
    }

    try {
      const { data: likesData, error: likesError } = await supabase
        .from('likes')
        .select('post_id')
        .eq('pol_id', currentUserId)

      if (likesError) throw likesError

      const userIds = likesData.map(like => like.post_id)
      const { data: userData, error: userError } = await supabase
        .from('datingbase')
        .select('*')
        .in('user_id', userIds)

      if (userError) throw userError

      setLikes(userData)
    } catch (error) {
      console.error('Error fetching data:', error)
    } finally {
      setIsLoading(false)
    }
  }

  const createParticles = (element) => {
    const rect = element.getBoundingClientRect();
    const particles = new Float32Array(PARTICLE_COUNT * PARTICLE_PROP_COUNT);
    
    for (let i = 0; i < PARTICLE_COUNT; i++) {
      const i5 = i * PARTICLE_PROP_COUNT;
      particles[i5] = Math.random() * rect.width + rect.left;
      particles[i5 + 1] = Math.random() * rect.height + rect.top; 
      particles[i5 + 2] = (Math.random() - 0.5) * 2; 
      particles[i5 + 3] = -Math.random() * 2 - 1; 
      particles[i5 + 4] = 1; 
    }

    return particles;
  }

  const animateParticles = (ctx, particles, image, startTime) => {
    const progress = (Date.now() - startTime) / ANIMATION_DURATION;
    
    if (progress >= 1) {
      cancelAnimationFrame(animationRef.current);
      ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);
      return;
    }

    ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);

    for (let i = 0; i < PARTICLE_COUNT; i++) {
      const i5 = i * PARTICLE_PROP_COUNT;
      particles[i5] += particles[i5 + 2];
      particles[i5 + 1] += particles[i5 + 3];
      particles[i5 + 4] -= 0.02;

      if (particles[i5 + 4] > 0) {
        ctx.globalAlpha = particles[i5 + 4];
        ctx.drawImage(
          image,
          particles[i5] - 1,
          particles[i5 + 1] - 1,
          2,
          2,
          particles[i5] - 1,
          particles[i5 + 1] - 1,
          2,
          2
        );
      }
    }

    animationRef.current = requestAnimationFrame(() => animateParticles(ctx, particles, image, startTime));
  }

  const handleDelete = async (userId) => {
    const currentUserId = window.Telegram?.WebApp?.initDataUnsafe?.user?.id
    if (!currentUserId) {
      console.error('User ID not available')
      return
    }

    try {
      // Delete the like from the database
      const { error } = await supabase
        .from('likes')
        .delete()
        .eq('pol_id', currentUserId)
        .eq('post_id', userId)

      if (error) throw error

      // If deletion was successful, proceed with the animation and UI update
      const cardElement = document.getElementById(`card-${userId}`);
      if (cardElement && canvasRef.current) {
        cardElement.classList.add('deleting');
        
        const canvas = canvasRef.current;
        const ctx = canvas.getContext('2d');
        const particles = createParticles(cardElement);
        
        const image = new Image();
        image.crossOrigin = "anonymous";
        image.onload = () => {
          animateParticles(ctx, particles, image, Date.now());
        };
        image.src = cardElement.querySelector('img').src;

        setTimeout(() => {
          setLikes(likes.filter(user => user.user_id !== userId));
        }, ANIMATION_DURATION);
      }
    } catch (error) {
      console.error('Error deleting like:', error)
    }
  };

  if (selectedUser) {
    return (
      <div className="fixed inset-0 bg-gradient-to-b from-[#0A1829] to-black text-white">
        <div className="h-full flex flex-col">
          <div className="p-4 flex items-center">
            <button 
              onClick={() => setSelectedUser(null)} 
              className="flex items-center text-white/80 hover:text-white"
            >
              <ArrowLeft className="w-6 h-6 mr-2" />
              
            </button>
          </div>

          <div className="flex-1 overflow-auto">
            <div className="relative h-[70vh]">
              <img 
                src={selectedUser.image_url} 
                alt={selectedUser.name} 
                className="w-full h-full object-cover"
              />
              <div className="absolute bottom-0 left-0 right-0 p-6 bg-gradient-to-t from-black via-black/50 to-transparent">
                <h2 className="text-3xl font-semibold mb-2">
                  {selectedUser.name}, {selectedUser.age}
                </h2>
                <p className="text-lg text-white/90">{selectedUser.city}</p>
              </div>
            </div>
            <div className="p-6">
              <p className="text-lg text-white/90">{selectedUser.bio}</p>
            </div>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="fixed inset-0 bg-gradient-to-b from-[#0A1829] to-black text-white">
      <canvas ref={canvasRef} className="particle-canvas" width={window.innerWidth} height={window.innerHeight} />
      <div className="p-4 flex items-center justify-between">
        <button 
          onClick={onBack} 
          className="flex items-center text-white/80 hover:text-white"
        >
          <ArrowLeft className="w-6 h-6 mr-2" />
          
        </button>
        <div className="w-10" />
      </div>

      <div className="p-4 grid grid-cols-2 gap-4">
        {likes.map(user => (
          <div key={user.user_id} id={`card-${user.user_id}`} className="relative aspect-[3/4] rounded-3xl overflow-hidden">
            <div className="card-content absolute inset-0">
              <img
                src={user.image_url}
                alt={user.name}
                className="absolute inset-0 w-full h-full object-cover"
                onClick={() => setSelectedUser(user)}
              />
              <div className="absolute bottom-4 left-4 right-4 flex justify-between gap-4">
                <button 
                  className="flex-1 bg-white rounded-full py-3 flex items-center justify-center"
                  onClick={() => handleDelete(user.user_id)}
                >
                  <X className="w-6 h-6 text-black" />
                </button>
                <button className="flex-1 bg-white rounded-full py-3 flex items-center justify-center">
                  <Heart className="w-6 h-6 text-black" />
                </button>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* Navigation container (moved to bottom) */}
      <div className="nav-container fixed bottom-0 left-0 right-0 bg-black/50 backdrop-blur-sm pb-6">
        <div className="nav-content flex justify-around items-center p-4">
          <button className="p-3 text-white/80 hover:text-white">
            <Zap className="w-6 h-6" />
          </button>
          
          <button className="p-3 text-white/80 hover:text-white relative">
            <Heart onClick={() => navigate('/LikesPage')}  className="w-6 h-6" />
            <span className="absolute -top-1 -right-1 w-5 h-5 bg-red-500 rounded-full text-white text-xs flex items-center justify-center">
              8
            </span>
          </button>
          
          <button 
            onClick={() => navigate('/ProfilePage')} 
            className="p-3 text-white/80 hover:text-white"
          >
            <User className="w-6 h-6" />
          </button>
        </div>
      </div>
    </div>
  )
}

