import React, { useState, useEffect } from 'react'
import { createClient } from '@supabase/supabase-js'
import { User, Flame, X, Heart, Zap, Loader, CheckCircle } from 'lucide-react'
import { useNavigate } from 'react-router-dom';
import LikesPage from './LikesPage'
import './index.css'

const supabase = createClient('https://kjzpfvjyalipijwoesoh.supabase.co', 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6ImtqenBmdmp5YWxpcGlqd29lc29oIiwicm9sZSI6ImFub24iLCJpYXQiOjE3Mjg4MzU3ODMsImV4cCI6MjA0NDQxMTc4M30.JaIDZ6LDvHFa4jptS1BfBTzjMOcAu7ig1PqTHWgqJEg')

export default function Component() {
  const [cards, setCards] = useState([])
  const [currentIndex, setCurrentIndex] = useState(0)
  const [swipeDirection, setSwipeDirection] = useState(null)
  const [currentUser, setCurrentUser] = useState(null)
  const [isLoading, setIsLoading] = useState(true)
  const [showLikesPage, setShowLikesPage] = useState(false)
  const navigate = useNavigate();

  const handleProfilePage = () => {
    navigate('/ProfilePage')
  }

  useEffect(() => {
    const setAppHeight = () => {
      const doc = document.documentElement
      doc.style.setProperty('--app-height', `${window.innerHeight}px`)
    }

    if (window.Telegram && window.Telegram.WebApp) {
      const tg = window.Telegram.WebApp
      tg.ready()
      tg.expand()
      tg.enableClosingConfirmation()
      tg.disableVerticalSwipes()
    }
    
    document.documentElement.style.removeProperty('position')
    document.body.style.removeProperty('position')
    
    setAppHeight()
    
    window.addEventListener('resize', setAppHeight)
    window.addEventListener('orientationchange', setAppHeight)
    
    getCurrentUser()
    
    return () => {
      window.removeEventListener('resize', setAppHeight)
      window.removeEventListener('orientationchange', setAppHeight)
    }
  }, [])

  useEffect(() => {
    if (currentUser) {
      fetchProfiles()
    }
  }, [currentUser])

  async function fetchProfiles() {
    if (!currentUser) return

    setIsLoading(true)
    try {
      // Fetch current user's preferences
      const { data: userPreferences, error: userError } = await supabase
        .from('datingbase')
        .select('interespol, interesold, interescity')
        .eq('user_id', currentUser.id)
        .single()

      if (userError) throw userError

      let query = supabase
      .from('datingbase')
      .select('*')
      .neq('user_id', currentUser.id)

      if (userPreferences.interespol !== '') {
        query = query.eq('sex', userPreferences.interespol)
      }

      const selectedAge = userPreferences.interesold;
      query = query.gte('age', selectedAge)
                  .lte('age', selectedAge + 2)

      if (userPreferences.interescity !== '') {
        query = query.eq('city', userPreferences.interescity)
      }

      const { data, error } = await query


      if (error) throw error

      setCards(data)
      preloadImages(data)
    } catch (error) {
      console.error('Error fetching profiles:', error)
      setIsLoading(false)
    }
  }

  function preloadImages(profiles) {
    const imagePromises = profiles.map(profile => {
      return new Promise((resolve, reject) => {
        const img = new Image()
        img.src = profile.image_url
        img.onload = resolve
        img.onerror = reject
      })
    })

    Promise.all(imagePromises)
      .then(() => setIsLoading(false))
      .catch(error => {
        console.error('Error preloading images:', error)
        setIsLoading(false)
      })
  }

  function getCurrentUser() {
    if (window.Telegram && window.Telegram.WebApp) {
      const user = window.Telegram.WebApp.initDataUnsafe.user
      if (user) {
        setCurrentUser(user)
      } else {
        console.error('User data not available in Telegram WebApp')
      }
    } else {
      console.error('Telegram WebApp not available')
    }
  }

  const handleSwipe = async (direction) => {
    setSwipeDirection(direction)
    
    setTimeout(() => {
      setCurrentIndex((prevIndex) => (prevIndex + 1) % cards.length)
      setSwipeDirection(null)
    }, 300)

    if (direction === 'right' && currentUser && cards[currentIndex]) {
      try {
        const { error } = await supabase
          .from('likes')
          .insert({
            post_id: currentUser.id,
            pol_id: cards[currentIndex].user_id
          })
        
        if (error) {
          console.error('Error recording like:', error)
        } else {
          console.log('Like recorded successfully')
        }
      } catch (error) {
        console.error('Error in handleSwipe:', error)
      }
    }
  }

  if (showLikesPage) {
    return <LikesPage onBack={() => setShowLikesPage(false)} />
  }

  if (isLoading) {
    return (
      <div className="app-container flex items-center justify-center">
        <Loader className="w-12 h-12 text-blue-500 animate-spin" />
      </div>
    )
  }

  return (
    <div className="app-container">
      {/* Main content area */}
      <div className="main-content">
        {cards.map((card, index) => (
          <div
            key={index}
            className={`absolute inset-0 transition-all duration-300 ease-in-out
              ${index === currentIndex ? 'card-enter' : ''}
              ${swipeDirection === 'left' && index === currentIndex ? 'card-exit-left' : ''}
              ${swipeDirection === 'right' && index === currentIndex ? 'card-exit-right' : ''}
              ${index !== currentIndex ? 'opacity-0' : 'opacity-100'}
            `}
          >
            <div className="w-full h-full relative">
              <img
                src={card.image_url}
                alt={card.name}
                className="w-full h-full object-cover"
              />
              
              {/* User info overlay */}
              <div className="absolute bottom-0 left-0 right-0 p-6 bg-gradient-to-t from-black via-black/50 to-transparent">
                {/* Status badges */}
                <div className="flex gap-2 mb-3">
                  <span className="px-3 py-1 bg-white/20 rounded-full text-sm backdrop-blur-sm">
                     
                  </span>
                  <span className="px-3 py-1 bg-white/20 rounded-full text-sm backdrop-blur-sm">
                    {card.city}
                  </span>
                </div>

                {/* Name and verification */}
                <div className="flex items-center gap-2 mb-2">
                  <h2 className="text-3xl font-semibold">{card.name}, {card.age}</h2>
                  <CheckCircle className="w-6 h-6 text-blue-500" />
                </div>

                {/* Bio */}
                <p className="text-lg text-white/90 mb-8">{card.bio}</p>

                {/* Action buttons */}
                <div className="flex justify-between items-center max-w-md mx-auto">
                  <button 
                    onClick={() => handleSwipe('left')}
                    className="w-16 h-16 flex items-center justify-center rounded-full bg-white/10 backdrop-blur-sm hover:bg-white/20 transition-colors"
                  >
                    <X className="w-8 h-8" />
                  </button>
                  <button 
                    className="w-16 h-16 flex items-center justify-center rounded-full bg-gradient-to-r from-purple-500 to-pink-500 hover:from-purple-600 hover:to-pink-600 transition-colors"
                  >
                    <Flame className="w-8 h-8" />
                  </button>
                  <button 
                    onClick={() => handleSwipe('right')}
                    className="w-16 h-16 flex items-center justify-center rounded-full bg-gradient-to-r from-blue-500 to-blue-600 hover:from-blue-600 hover:to-blue-700 transition-colors"
                  >
                    <Heart className="w-8 h-8" />
                  </button>
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* Navigation container (moved to bottom) */}
      <div className="nav-container fixed bottom-0 left-0 right-0 bg-black/50 backdrop-blur-sm pb-6">
        <div className="nav-content flex justify-around items-center p-4">
          <button className="p-3 text-white/80 hover:text-white">
            <Zap className="w-6 h-6" />
          </button>
          
          <button className="p-3 text-white/80 hover:text-white relative" onClick={() => setShowLikesPage(true)}>
            <Heart className="w-6 h-6" />
            <span className="absolute -top-1 -right-1 w-5 h-5 bg-red-500 rounded-full text-white text-xs flex items-center justify-center">
              8
            </span>
          </button>
          
          <button 
            onClick={handleProfilePage} 
            className="p-3 text-white/80 hover:text-white"
          >
            <User className="w-6 h-6" />
          </button>
        </div>
      </div>
    </div>
  )
}

